@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rambla&display=swap);
.header-section {
    display: flex;
    justify-content: center;
    width: 100%;
}

.header-left.active {
    width: 100%;
    background-image: url(/md-omar-faruk/static/media/dev.of.80a4b405.png);
    background-repeat: no-repeat;
    background-size: 300px 100px cover;
    margin: 0px auto;
}

.header-left {
    width: 35%;
    background: var(--Black);
    transition: 0.5s;
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 10;
    height: 100vh;
    background-image: url(/md-omar-faruk/static/media/dev.of.80a4b405.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-color: linear-gradient( to bottom, rgba(255, 255, 255, 0.15) 0%, rgba(0, 0, 0, 0.15) 100%), radial-gradient( at top center, rgba(255, 255, 255, 0.4) 0%, rgba(0, 0, 0, 0.4) 120%) #989898;
    background-blend-mode: multiply, multiply;
    margin: 0px;
}

.header-left:hover {
    background: #03e9f4;
    color: var(--White);
    box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4, 0 0 200px #03e9f4;
    background-image: url(/md-omar-faruk/static/media/dev.of.80a4b405.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.header-left p {
    left: 25%;
    top: 5%;
    font-size: 15px;
    font-weight: 300;
    position: absolute;
    font-family: var(--Oswald);
    color: var(--White);
    letter-spacing: 4px;
    text-transform: capitalize;
}

.header-left h3 {
    left: 25%;
    top: 8%;
    font-size: 27px;
    font-weight: 600;
    position: absolute;
    font-family: var(--Oswald);
    color: var(--Tomato);
    letter-spacing: 4px;
    text-transform: capitalize;
    overflow: hidden;
    transition: 0.5s;
    border-right: 1px solid var(--Tomato);
    -webkit-animation: typing 5s steps(10) infinite;
            animation: typing 5s steps(10) infinite;
}

.header-left:hover p {
    color: var(--PureBlack);
    font-size: 20px;
    font-weight: 600;
    transition: 0.5s;
}

.header-left:hover h3 {
    color: var(--White);
    border-right: 1px solid var(--White);
}

@-webkit-keyframes typing {
    0%,
    90%,
    100% {
        width: 0px;
    }
    30%,
    60% {
        width: 145px;
    }
}

@keyframes typing {
    0%,
    90%,
    100% {
        width: 0px;
    }
    30%,
    60% {
        width: 145px;
    }
}


/* Animation Text */

.header-left img {
    width: 100%;
    height: 100vh;
    margin: 0 auto;
    display: block;
    object-fit: cover;
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 10;
}

.header-right {
    width: 65%;
}

.header-right.active {
    width: 100%;
    display: none;
}

.social-icon.active {
    bottom: 30%;
    left: 0%;
}

.social-icon {
    bottom: 30%;
    left: 0%;
    position: absolute;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

.s-icon {
    text-align: center;
    font-size: 15px;
    height: 40px;
    width: 40px;
    line-height: 40px;
    padding: 12px;
    margin: 0px 10px 10px 0px;
    border: none;
    border-radius: 50%;
    color: var(--White);
    background-color: var(--Tomato);
    cursor: pointer;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
}

.s-icon:hover {
    background-color: var(--PureBlack);
    transition: 0.5s;
}

.particles {
    width: 35vw;
    height: 99vh;
}

.particles.active {
    width: 99vw;
    height: 99vh;
}

@media (max-width: 1024px) {
    .header-section {
        display: inline;
        width: 90%;
    }
    .header-left {
        width: 100%;
        position: relative;
    }
    .header-left.active {
        width: 100%;
        background-image: url(/md-omar-faruk/static/media/dev.of.80a4b405.png);
        background-repeat: no-repeat;
        background-size: 300px 100px cover;
    }
    .header-left img {
        position: relative;
    }
    .header-right {
        width: 100%;
    }
    .header-right.active {
        width: 100%;
        display: none;
    }
    .social-icon {
        bottom: 20%;
        left: 0%;
    }
    .social-icon.active {
        bottom: 20%;
        left: 0%;
    }
    .header-left p {
        margin-left: -15%;
        top: 5%;
    }
    .header-left h3 {
        margin-left: -15%;
        top: 8%;
    }
    .particles {
        width: 100%;
        height: 100vh;
    }
    .particles.active {
        width: 100%;
        height: 100vh;
    }
}

@media (max-width: 768px) {
    .header-section {
        display: inline;
        width: 100%;
    }
    .header-left {
        width: 100%;
    }
    .header-left.active {
        width: 100%;
        background-image: url(/md-omar-faruk/static/media/dev.of.80a4b405.png);
        background-repeat: no-repeat;
        background-size: 300px 100px cover;
    }
    .header-right {
        width: 100%;
    }
    .header-right.active {
        width: 100%;
        display: none;
    }
    .social-icon {
        top: 70%;
        margin-left: -4rem;
    }
    .social-icon.active {
        top: 70%;
        margin-left: -4rem;
    }
    .header-left p {
        margin-left: 0rem;
        top: 5%;
    }
    .header-left h3 {
        margin-left: 0rem;
        top: 8%;
    }
    .s-icon {
        height: 35px;
        width: 35px;
        padding: 10px;
        margin: 0px 8px 8px 0px;
    }
    .particles {
        width: 100%;
        height: 100vh;
    }
    .particles.active {
        width: 100%;
        height: 100vh;
    }
}
.navbar {
    top: 0px;
    padding: 15px 0px 15px 0px;
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    left: 0;
    right: 0;
    z-index: 10;
    background-color: var(--Black);
    /* border-bottom: 1px solid var(--Tomato); */
}

.navbar__text {
    display: flex;
    justify-content: center;
    height: 60px;
    align-items: center;
    margin: 0 auto;
    /* border-bottom: 1px solid var(--Tomato); */
}

.navbar__ul {
    list-style-type: none;
    display: flex;
    justify-content: center;
}

.nav-menu li a {
    text-decoration: none;
    color: var(--Gray);
    font-family: var(--Oswald);
    padding: 0px 0px;
    margin: 0px 15px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 2px;
    display: inline-block;
    position: relative;
}

.nav-menu li a:after {
    content: "";
    position: absolute;
    width: 100%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    height: 1px;
    bottom: 0;
    left: 0;
    background-color: var(--Tomato);
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    transition: -webkit-transform 0.25s ease-out;
    transition: transform 0.25s ease-out;
    transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.nav-menu li a:hover:after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
}

.nav-menu {
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-left: 2rem;
    width: 60%;
    list-style-type: none;
    display: flex;
    justify-content: center;
}

.nav-item {
    display: flex;
    align-items: center;
    height: 80px;
}

.nav-links {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover {
    color: var(--Tomato);
    border-radius: 4px;
    transition: all 0.3s ease-out;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media (max-width: 1024px) {
    .navbar {
        width: 100%;
        position: -webkit-sticky;
        position: sticky;
        left: 0;
        right: 0;
        padding: 0px 0px 0px 0px;
        top: 0;
        z-index: 10;
    }
    .navbar__text {
        display: flex;
        height: auto;
        align-items: center;
        width: 924px;
        margin: 0 auto;
    }
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 80%;
        height: 60vh;
        position: absolute;
        left: 10%;
        top: -200rem;
        opacity: 1;
        transition: all 0.5s ease;
        margin: 0 0;
        z-index: 1;
    }
    .nav-menu.active {
        background: var(--Black);
        top: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
        border: 1px solid Var(--Gray);
    }
    .nav-links {
        text-align: center;
        width: 100%;
        display: table;
    }
    .nav-links:hover {
        border-radius: 0;
    }
    .menu-icon {
        display: inline-block;
        position: absolute;
        top: 0;
        right: 0;
        -webkit-transform: translate(-100%, 60%);
                transform: translate(-100%, 60%);
        cursor: pointer;
        z-index: 10;
    }
    .fa-ellipsis-v,
    .fa-ellipsis-h {
        border: 2px solid;
        border-radius: 50%;
        padding: 5px 5px;
        font-size: 25px;
        margin-left: -1.5rem;
        margin-top: 0.6rem;
        color: var(--Black);
        background-color: var(--Tomato);
    }
    .fa-ellipsis-v:hover,
    .fa-ellipsis-h:hover {
        color: var(--White);
        transition: 0.5s ease;
    }
}

