}

.about-text-header-p {
    margin: 30px 0px;
}

.about-text-header img {
    vertical-align: middle;
    display: inline-block;
    height: auto;
    width: 200px;
    z-index: 10;
}

.hover-underline-animation {
    display: inline-block;
    position: relative;
    color: var(--Tomato);
    text-decoration: none !important;
}

.hover-underline-animation:after {
    content: "";
    position: absolute;
    width: 100%;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: var(--Tomato);
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    transition: -webkit-transform 0.25s ease-out;
    transition: transform 0.25s ease-out;
    transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.hover-underline-animation:hover:after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left;
}

.about-text-info {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    justify-content: space-between;
}

.about-text-info p {
    color: var(--White);
    font-size: 15px;
    font-weight: bold;
    line-height: 1.75em;
    padding: 10px 0px;
}

.about-text-info a {
    color: var(--Gray1);
    font-size: 15px;
    font-weight: 400;
    line-height: 1.75em;
    font-family: var(--Rambla);
    text-decoration: none !important;
    padding-left: 5px;
}

.hire-btn {
    background: var(--Tomato);
    font-family: var(--Rambla);
    border: none;
    color: var(--White);
    text-align: center;
    font-size: 16px;
    font-weight: 400;
    padding: 8px 20px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 20px 0px;
}

.hire-btn span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

.hire-btn span:after {
    content: "\00bb";
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

.hire-btn:hover span {
    padding-right: 25px;
}

.hire-btn:hover span:after {
    opacity: 1;
    right: 0;
}

.service-card {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    position: relative;
    margin: 0px auto;
}

.sv-card {
    width: 45%;
    height: 100%;
    margin: 16px 17.5px 16px 17.5px;
    padding: 20px;
    border: 1px solid var(--Gray1);
    text-align: center;
}

.sv-card img {
    background-color: var(--Tomato);
    border-radius: 50%;
    margin: 10px 0px;
    width: 50px;
    transition: width 1s, height 1s, -webkit-transform 1s;
    transition: width 1s, height 1s, transform 1s;
    transition: width 1s, height 1s, transform 1s, -webkit-transform 1s;
    cursor: pointer;
}

.sv-card img:hover {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
