    font-size: 20px;
    color: var(--White);
    background-color: var(--PureBlack);
}

@-webkit-keyframes float {
    0%,
    100% {
        -webkit-transform: translateY(-0.5rem);
                transform: translateY(-0.5rem);
    }
    50% {
        -webkit-transform: translateY(0.5rem);
                transform: translateY(0.5rem);
    }
}

@keyframes float {
    0%,
    100% {
        -webkit-transform: translateY(-0.5rem);
                transform: translateY(-0.5rem);
    }
    50% {
        -webkit-transform: translateY(0.5rem);
                transform: translateY(0.5rem);
    }
}

@media (max-width: 1024px) {
    .scrollTop-btn {
        left: 50%;
        font-size: 2.5rem;
    }
}

@media (max-width: 768px) {
    .scrollTop-btn {
        left: 85%;
        font-size: 1.5rem;
    }
}
